
.main {
  width: 100vw;
  background-image: linear-gradient(
    0deg,
    #000000 0%,
    #d3d3d3 79%,
    #ffffff 100%
  );
  padding: 5px;
  
}

/* HeaderImage.css */
.header-image-container {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.header-image {
  max-width: 1070px;
  height: auto;
}

.scroll-indicator {
  width: 200px;
  background: none;
  position: absolute;
  bottom: 20px; /* Adjust as needed */
  left: 100px;
  border-top: 2px solid #fff;
  text-align: center;
  color: #fff; /* Text color */
  font-size: 12px; /* Adjust font size as needed */
  transform: rotate(-90deg); /* Rotate the text by 90 degrees */
  transform-origin: left bottom; /* Rotate from the left bottom corner */
}

.scroll-indicator div {
  width: fit-content;
  text-align: left;
  border-top: 5px solid #fff;
}

section {
  width: 100vw;
  background-image: linear-gradient(0deg, #000000 0%, #000000 100%);
  color: white;
}

.header-content{
    margin: 0px 5rem;
    padding: 50px 0px;
}

section h1 {
    padding-top: 2rem;
    width: fit-content;
  font-size: 70px;
  letter-spacing: -3px;
  color: #ffffff;
  font-weight: bold;
  font-family: "Neusharp Bold";
}

video {
    width: 100%;
    max-width: 800px; /* Adjust maximum video width as needed */
    display: block;
    margin: 20px auto;
  }



  /* Adjust font size for smaller screens */
@media screen and (max-width: 1100px) {
 .footerVerticals{
  display: none;
 }
 .features{
  display: none;
 }

}
/* Adjust font size for smaller screens */
@media screen and (max-width: 768px) {
    .header-content h1 {
      font-size: 40px; /* Adjust font size for smaller screens */
    }
  }
  
  /* Further adjustments for even smaller screens if needed */
  @media screen and (max-width: 576px) {
    .header-content h1 {
      font-size: 16px; /* Adjust font size for even smaller screens */
    }
  }