.dignitaries{
    background-color: #f7f7f7;
    margin: 2rem auto;
    padding: 4rem 6rem;
    height: fit-content;
}
.dignitaries> header{
    color: #382224;;
}

.dignitaries > header > h1{
    font-family: "Neusharp Bold";
    font-size: 3.5rem;
    
}
.msg-container{
    display: flex;
    justify-content: space-around;
    gap: 3rem;
    height: 28rem;
}

.dignitaries .profile{
    text-align: center;
}

.dignitaries .profileName{
    background-color: #eca404;
    padding: 1rem 0rem;
    margin-top: 1rem;
}

.dignitaries .profileImg {
    border-radius: 50%;
    overflow: hidden;
    max-height: 22rem;
    width: 20rem;
}

.dignitaries  img {
    width: 100%;
    object-fit: contain;
    
}

.dignitaries .img img {
    height: 100%;
    object-fit: contain;
}

.dignitaries .img-container {
    display: flex;
}


@media screen and (max-width: 1000px) {
 .dignitaries{
    text-align: center;
 }
 .msg-container{
    flex-wrap: wrap;
    height: auto;
 }
   }