/* AboutCard.css */
.about-card {
  width:20rem;
  min-width: 5rem;
  border-radius: 5px;
  
  margin: 2em;
  text-align: center;
}

.about-card-img-container {
  padding: 1rem 0.3rem;
  background: url("../../../assets//images//aboutCard/cardBg.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.about-card-image {
  max-width:100%;
  height: 6rem;
  object-fit: contain;
}

.about-card-heading {
  font-family: "Neusharp bold";
  font-size: 3rem;
  margin: 10px 0;
}

.about-card-subheading {
  font-size: 1.2rem;
  margin: 10px 0;
  
}

.about-card-text {
  font-size: 1rem;
  margin-top: 10px;
}


/* Adjust font size for smaller screens */
@media screen and (max-width: 768px) {
    .about-card{
        margin: 1rem;
    }
    .about-card-heading {
      font-size: 2rem;
    }
    .about-card-image{
      max-width: 70%;
    }
  }
