/* verticalsCard.css */
.verticals-card {
  width: 250px; /* Set the width of each card */
  height: 300px; /* Set the height of each card */
  margin: 10px;
  position: relative;
  background-color:  #1c1112;

  overflow: hidden;
  /* display: flex;
    flex-direction: column;
    justify-content: flex-end; */
}

.verticals-card img {
  width: inherit;
}

.verticals-card-content {
  background: linear-gradient(
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 0.4) 25%,
    rgba(0, 0, 0, 0.6) 50%,
    rgba(0, 0, 0, 0.8) 75%,
    rgba(0, 0, 0, 1) 100%
  ); /* Semi-transparent background for text */
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 130px;
  bottom: 0px;
  padding: 1rem;
  text-align:left;
  color: #fff;
}

.verticals-card-heading {
  font-size: 1rem;
  margin: 0;
  padding-bottom: 5px;
}

.verticals-card-text {
  font-size: 0.7rem;
  line-height: 1.2rem;
  margin: 0;
  padding-top: 5px;
}
