.aboutSection {
    font-family: "Helvetica Now Display","sans-serif";
    color: #ffffff;
}

.aboutSection h1 {
  margin: 0;
  font-size: 5rem;
  font-family: "Neusharp Bold"
}

.aboutSection h4 {
  font-size: 2rem;
  font-weight: 200;
  letter-spacing: 3px;
  margin-bottom: -20px;
}
.aboutSection h4 span {
  color: yellow;
  letter-spacing: 0px;
}

.aboutSection h5{
    font-size: 1.5rem;
    font-weight: 300;
}


.aboutSection p{
    padding-top: 1rem;
    line-height: 1.5rem;
    font-size: 1.1rem;
}


.know-more-button {
    background-color: #ffc107; /* Yellow background color */
    background: none;
    color: #ffffff;
    padding: 10px 0px; /* Adjust padding as needed */
    margin: 1rem 0;
    border: none;
    border-radius: 5px;
    font-size: 16px; /* Adjust font size as needed */
    cursor: pointer;
     /* Adjust margin as needed */
  }

  
  .yellow-square {
    width: 20px;
    height: 20px;
    background-color: #ffc107; /* Yellow square color */
    display: inline-block;
    margin-left: 5px; /* Adjust margin as needed */
  }


  .card-container{
    display: flex;
    
  }