.icprdVerticals {
  padding: 3rem 0rem;
  position: relative;
  overflow-x: hidden;
}

.icprdVerticals > span {
  position: absolute;
  opacity: 0.078;
  font-size: 31vw;
  top: -4rem;
  left: -1.5rem;
  /* line-height: 81px; */
  color: #032648;
  font-weight: bold;
  font-family: "Neusharp Bold";
  text-transform: uppercase;
}

.icprdVerticals h1 {
  font-family: "Neusharp bold";
  text-align: center;
  color: #382224;
  font-size: 3rem;
}
.icprdVerticals > p {
  color: #382224;
  font-weight: bold;
  text-align: center;
}
.text-with-line::before,
.text-with-line::after {
  content: "";
  display: inline-block;
  width: 70px; /* Adjust the width of the lines */
  height: 2px; /* Adjust the height/thickness of the lines */
  border-radius: 5px;
  background-color: #eca404; /* Adjust the color of the lines */
  margin: 0 10px; /* Adjust the spacing between the lines and text */
  margin-bottom: 0.3rem;
}

.vertical-card-container {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
