

.icprdEvents header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.icprdEvents header h1{
    font-size: 3.5rem;
}

.icprdEvents header p,.icprdEvents header h1{
    font-family: "Neusharp Bold";
}

.icprdEvents header button{
    background: #eca404;
    border: none;
    padding: 1.3rem 2rem;
    font-size: 1.5rem;
    color: #fff;
    border-radius: 5px;
}

.icprdEvents .event-section{
    display: flex;
    overflow-x: auto;
}

 /* Further adjustments for even smaller screens if needed */
 @media screen and (max-width: 370px) {
    .icprdEvents{
        padding: 4rem 0rem ;
        text-align: center;
    }
    .icprdEvents header{
        display: block;
    }
  }