/* EventCard.css */
.event-card {
    width: 100%; /* Set the width of the card */
    border: 1px solid #ddd;
    border-radius: 20px;
    margin: 20px;
    text-align: center;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    background-color: black;
    color: #fff;
    min-width: 300px;
  }
  
  .event-card-image {
    width: 100%;
    height: 300px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    object-fit: contain; /* Set the height of the image */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    
  }

  .event-card-content{
    padding: 1.5rem;
    text-align: left;
  }
  
  .event-card-heading {
    font-size: 1.3rem;
    margin: 1rem 0;
    color: white;
    text-align: left;    
  }
  
  .event-card-hr {
    border: none;
    height: 3px;
    width: 30%;
    background-color: #eca404;
    margin: 1rem 0;
  }
  
  .event-card-detail{
    margin: 0.2rem auto;
  }
  