.dynamicHr {
display: flex;
gap: 1px;
align-items: center;
margin: 1rem auto;
}

.dynamicHr hr{
    border: none;
}

.dynamicHr .line{
    height: 1px;
    flex: 1;
    background-color: #ffffff6f
    
};

.dynamicHr .dark-hr{
    background-color:#bfbfbf
};

.dynamicHr .light-hr{
    background-color:#ffffff33
}

.dynamicHr .yellow-line{
width: 50px;
height: 7px;
border-radius:5px;
background-color: #eca404;
}

.dynamicHr .light-line{
    width: 13px;
    height: 7px;
    border-radius:5px;
    background-color: white;
}

.dynamicHr .dark-line{
    width: 13px;
    height: 7px;
    border-radius:5px;
    background-color: black;
}