/* Navbar.css */
/* Normal navbar styles */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    padding-top: 2rem;
  }
  
  .navbar-logo {
    display: flex;
    align-items: center;
  }
  .navbar-logo span {
    padding-left: 0.7rem;
    border-left: 2px solid #A25152;
    height: fit-content;
    font-weight: bold;
    color: #A25152;
  }
  
  .navbar-logo img {

    margin-right: 10px;
  }
  
  .navbar-menu {
    display: flex;
    list-style: none;
  }
  
  .navbar-menu li {
    margin-right: 20px;
  
  }
  
  .navbar-menu a {
    text-decoration: none;
    font-size: 18px;
    line-height: 39px;
    color: #382224;
    font-weight: 500;
    
  }
  
  /* Hamburger menu styles for small screens */
  @media screen and (max-width: 768px) {
    .navbar-menu {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      right: 0;
      background-color: #333;
      z-index: 1;
    }
  
    .navbar-menu.active {
      display: flex;
    }
  
    .navbar-menu li {
      margin: 10px 0;
    }
  
    .navbar-hamburger {
      display: flex;
      flex-direction: column;
      cursor: pointer;
    }
  
    .bar {
      width: 25px;
      height: 3px;
      background-color: #fff;
      margin: 3px 0;
    }
  
    .bar.open {
      transform: rotate(-45deg) translate(-5px, 5px);
    }
  }
  