.featureCard {
  position: relative;
  width: 33.3vw;
  height: 600px;
  background-color: rgb(2, 2, 2);
}

.featureCard img {
  width: inherit;
}

.feature-card-content {
 /* Semi-transparent background for text */
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 50%;
  bottom: 0px;
  padding: 1rem 6rem;
  text-align: left;
  color: #fff;
}

.feature-card-content > hr{
    width: 35%;
    border: none;
    height: 3px;
    color:  #eca404;;
    background-color: #eca404;
}

.feature-card-heading {
    width: min-content;
    font-size: 2rem;
  font-family: "Neusharp Bold";
  line-height: 2rem;
  white-space: pre-wrap; /* Preserve whitespace */
  word-wrap: break-word; /* Allow breaking of words */
  margin: 0;
  padding-bottom: 5px;
  text-transform: uppercase;
}

.feature-card-text {
  font-size: 0.7rem;
  line-height: 1.2rem;
  margin: 0;
  padding-top: 5px;
}
